#DivInputsMargins {
  margin-bottom: 12px;
}

#Flexbox_Div_inputs {
  margin-top: 59px;
  display: flex;
  justify-content: space-around;
  /* justify-content: center; */

  /* position: absolute; */
  /* top:122px; */

  width: 100%;
